import MovieList from '../../components/MovieList.vue'
// import {
// 	getInTheaters
// } from '../../service/inTheaters.js'
export default {
	data() {
		return {
			itemList: {
				'影院热映': '/in_theaters',
				'即将上映': '/coming_soon',
				'Top250': '/top250',
				'口碑榜': '/weekly',
				'北美票房榜': '/us_box',
				'新片榜': '/new_movies'
			},
			// /new_movies
			// /top250
			// /weekly
			// /us_box
			// /in_theaters
			// /coming_soon
		}
	},
	components: {
		MovieList
	},
	methods: {
		funTimestamp(delay) {
			const now = Date.parse(new Date())
			if (uni.getStorageSync('timestamp')) {
				const timestamp = uni.getStorageSync('timestamp')
				const timeout = timestamp + delay
				if (now > timeout) {
					uni.removeStorageSync('timestamp')
				} else {
					console.log('数据未到过期时间')
				}
			} else {
				const timestamp = Date.parse(new Date())
				uni.setStorageSync('timestamp', timestamp)
			}
		},
		handleToSearch() {
			uni.navigateTo({
				url: '../Search/Search'
			})
		}
	},
	mounted() {
		this.funTimestamp(1000 * 60 * 60)
	}
}
